import React, { Component } from 'react';
import Widecard from '../components/Widecard';
class Projects extends Component {
render() {
return (
<div className="projects">
<h1 className="subtopic">My Projects</h1>
<Widecard 
title="To-Do List" 
blurb="A simple, fully functioning to-do list. Makes good use of React concepts." 
live={<a href="https://joeymonachino.github.io/todolist/" target="_blank">View Live</a>} 
github={<a href="https://github.com/joeymonachino/todolist" target="_blank">View Code</a>}
/>
<Widecard 
title="Smite Society" 
blurb="My very first React project built by me from the ground up. This was just to get me started." 
live={<a href="https://joeymonachino.github.io/smitesociety/" target="_blank">View Live</a>}  
github={<a href="https://github.com/joeymonachino/smitesociety" target="_blank">View Code</a>}
/>
</div>
)
}
}
export default Projects


